import React from 'react';
import { Link } from "react-router-dom";
import logo from '../images/logo.png';
import '../styles/Header.css';
let origin = window.origin.toString();

class Header extends React.Component {

    state = {

    };

    componentDidMount () {

    }

    toggleClass = (e) => {
        e.preventDefault();
        let otherElems = document.querySelectorAll('.header__elem1__link');
        otherElems.forEach( elem => elem.classList.remove('--selected'));
        let elem = e.currentTarget.classList;
        elem.add('--selected');
    };

    render () {
        return(

            <div className={"header"}>
                <div className={"header__elem1"}>
                    <div className={"header__elem1__logo"}> <img src={logo} width={40} height={40}/></div>
                    <div className={"header__elem1__link quick"} onClick={this.toggleClass}> <Link to="/advent2020">사도강림2020</Link> </div>
                    <div className={"header__elem1__link quick"} onClick={this.toggleClass}> <Link to="/aradschool">왕립아라드</Link> </div>
                    <div className={"nav-vertical-bar"}>  </div>
                    <div className={"header__elem1__link search"} onClick={this.toggleClass}> <Link to="/wd">검색</Link> </div>
                    <div className={"header__elem1__link about"} onClick={this.toggleClass}> <Link to="/about">정보</Link> </div>
                </div>

                <div className={"header__elem2"}>
                    <div> { origin.includes('osh') ? 'public' : 'live-dev' } </div>
                </div>
            </div>
            )
    }
}

export default Header;